<template>
    <div class="header">
        <div class="navigation">
            <div class="div-block-2">
                <a v-on:click="navToPath('/')" class="nav-item">
                    <img src="../../assets/images/logo-light.svg" alt="" class="image">
                </a>
                <div class="div-block-3">
                    <div class="space-heading">
                        <div class="inline">Space:</div>
                    </div>
                    <div class="inline text-light"><a v-on:click="navToPath('teams')" class="nav-item">{{user.teamName}}</a></div>
                    <a v-on:click="navToPath('bases')" class="nav-item w-inline-block"><img src="../../assets/images/list_24px.svg" alt="" class="nav-item-icon"><div>Bases</div></a>
                    <a v-on:click="navToPath('users')" class="nav-item w-inline-block"><img src="../../assets/images/supervisor_account_24px.svg" alt="" class="nav-item-icon"><div>Users</div></a>
                </div>
            </div>
            <ul role="list" class="account-items">
                <li class="account-item"><router-link to="/faq">Docs</router-link></li>
                <li class="account-item"><router-link to="/account">Account</router-link></li>
                <li class="account-item"><a href="#" v-on:click="signout">Sign out</a></li>
            </ul>
        </div>
        <navbar-tables-selector v-if="showTables"></navbar-tables-selector>
    </div>
</template>
<script>
import { mapState, mapActions } from 'vuex';
import { Action } from '../../store/types';

export default {
    name: 'Navbar',
    components: {
        NavbarTablesSelector: () => import('./NavbarTablesSelector')
    },
    data: function() {
        return {
            activeTable: '',
            tables: []
        }
    },
    computed: {
        ...mapState({
            user: state => state.user,
            showTables: state => state.navigation.showTables
        })        
    },
    methods: {
        ...mapActions({
            logout: Action.LOGOUT
        }),
        signout() {
            this.logout();
        },
        navToPath(path) {
            this.$router.push(path);
        }
    }
}
</script>

<style>
.nav-item {
    cursor: pointer;
}
</style>